.App {
  height: 100vh;
  display: grid;
  grid-template-rows: 50px 1fr;
  grid-template-columns: 1fr;
  grid-template-areas: "header" "body";
}

.App__header {
  grid-area: header;
  background-color: var(--red);
  padding: 20px;
  color: var(--light);
}

.App__body {
  grid-area: body;
  position: relative;
  background-color: var(--light);
}

.displayToggle {
  position: fixed;
  bottom: 20px;
  right: 50px;
  background-color: var(--red);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  z-index: 2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.displayToggle i {
  color: var(--light);
}



